var render = function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('section',{staticClass:"blog_area single-post-area section-padding2"},[_c('div',{staticClass:"container"},[_c('div',{staticClass:"row "},[_c('div',{staticClass:"col-lg-9 col-md-11 col-sm-12 posts-list story center-block"},[_vm._m(0),_vm._m(1),_vm._m(2),_vm._m(3),_c('div',{staticClass:"comment-form"},[_c('h2',{staticClass:"mb-4"},[_vm._v("Leave A Comment")]),_vm._v("\n                        \n                            Please login and comment\n                            "),_c('a',{staticClass:"btn btn-primary",attrs:{"href":"/login/?from=/article/2020/8/10/2/let-s-debug-a-node-js-application.html"}},[_vm._v("log in")]),_c('span',[_vm._v(" or ")]),_c('div',{staticClass:"col-md-8"},[_c('ckeditor',{attrs:{"editor":_vm.editor,"config":_vm.editorConfig},model:{value:(_vm.editorData),callback:function ($$v) {_vm.editorData=$$v},expression:"editorData"}})],1)])])])]),_vm._m(4)])}
var staticRenderFns = [function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"section-tittle text-center"},[_c('h2',[_vm._v("Let's Debug a Node.js Application")])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{},[_c('div',{staticClass:"single-post"},[_c('div',{staticClass:"blog_details"},[_c('div',[_c('textarea',{attrs:{"id":"textToSpeech","hidden":""}},[_vm._v("There are always challenges when it comes to debugging applications. Node.js' asynchronous workflows add an extra layer of complexity to this arduous process. Although there have been some updates made to the V8 engine in order to easily access asynchronous stack traces, most of the time, we just get errors on the main thread of our applications, which makes debugging a little bit difficult. As well, when our Node.js applications crash, we usually need torely on some complicated CLI tooling to analyze the core dumps.In this article, we'll take a look at some easier ways to debug your Node.js applications.LoggingOf course, no developer toolkit is complete without logging. We tend to placeconsole.logstatements all over our code in local development, but this is not a really scalable strategy in production. You would likely need to do some filtering and cleanup, or implement a consistent logging strategy, in order to identify important information from genuine errors.Instead, to implement a proper log-oriented debugging strategy, use a logging tool likePinoorWinston. These will allow you to set log levels (INFO,WARN,ERROR), allowing you to print verbose log messages locally and only severe ones for production. You can also stream these logs to aggregators, or other endpoints, like LogStash, Papertrail, or even Slack.Working with Node Inspect and Chrome DevToolsLogging can only take us so far in understanding why an application is not working the way we would expect. For sophisticated debugging sessions, we will want to use breakpoints to inspect how our code behaves at the moment it is being executed.To do this, we can use Node Inspect. Node Inspect is a debugging tool which comes with Node.js. It's actually just an implementation ofChrome DevToolsfor your program, letting you add breakpoints, control step-by-step execution, view variables, and follow the call stack.There are a couple of ways to launch Node Inspect, but the easiest is perhaps to just call your Node.js application with the--inspect-brkflag:$ node --inspect-brk $your_script_name\n                    After launching your program, head to thechrome://inspectURL in your Chrome browser to get to the Chrome DevTools. With Chrome DevTools, you have all of the capabilities you'd normally expect when debugging JavaScript in the browser. One of the nicer tools isthe ability to inspect memory. You cantake heap snapshotsand profile memory usage to understand how memory is being allocated, and potentially, plug any memory leaks.Using a supported IDERather than launching your program in a certain way, many modern IDEs also support debugging Node applications. In addition to having many of the features found in Chrome DevTools, they bring their own features, such ascreating logpointsand allowing you to create multiple debugging profiles. Check outthe Node.js' guide on inspector clientsfor more information on these IDEs.Using NDBAnother option is to installndb, a standalone debugger for Node.js. It makes use of the same DevTools that are available in the browser, just as an isolated, local debugger. It also has some extra features that aren't available in DevTools. It supports edit-in-place, which means you can make changes to your code and have the updated logic supported directly by the debugger platform. This is very useful for doing quick iterations.Post-Mortem DebuggingSuppose your application crashes due to a catastrophic error, like a memory access error. These may be rare, but they do happen, particularly if your app relies on native code.To investigate these sorts of issues, you can usellnode. When your program crashes,llnodecan be used to inspect JavaScript stack frames and objects by mapping them to objects on the C/C++ side. In order to use it, you first need a core dump of your program. To do this, you will need to useprocess.abortinstead ofprocess.exitto shut down processes in your code. When you useprocess.abort, the Node process generates a core dump file on exit.To better understand whatllnodecan provide,here is a videowhich demonstrates some of its capabilities.Useful Node ModulesAside from all of the above, there are also a few third-party packages that we can recommend for further debugging.debugThe first of these is called, simply enough,debug. With debug, you can assign a specific namespace to your log messages, based on a function name or an entire module. You can then selectively choose which messages are printed to the console via a specific environment variable.For example, here's a Node.js server which is logging several messages from the entire application and middleware stack, likesequelize,express:application, andexpress:router:If we set theDEBUGenvironment variable toexpress:routerand start the same program, only the messages tagged asexpress:routerare shown:By filtering messages in this way, we can hone in on how a single segment of the application is behaving, without needing to drastically change the logging of the code.trace and clarifyTwo more modules that go together aretraceandclarify.traceaugments your asynchronous stack traces by providing much more detailed information on the async methods that are being called, a roadmap which Node.js does not provide by default.clarifyhelps by removing all of the information from stack traces which are specific to Node.js internals. This allows you to concentrate on the function calls that are just specific to your application.Neither of these modules are recommended for running in production! You should only enable them when debugging issues in your local development environment.Find out moreIf you'd like to follow along with how to use these debugging tools in practice,here is a video recordingwhich provides more detail. It includes some live demos of how to narrow in on problems in your code. Or, if you have any other questions, you can find me on Twitter@julian_duque!")])]),_c('ul',[_c('div',{staticClass:"author mb-4 d-flex align-items-center"},[_c('a',{staticClass:"img",staticStyle:{"background-image":"url(/static/app/img/blog/author.png)"},attrs:{"href":"#"}}),_c('div',{staticClass:"ml-3 info"},[_c('span',[_vm._v("Written by")]),_c('h3',[_c('a',{attrs:{"href":"/author/puneethreddyhc.html","title":"View all articles published by puneethreddyhc","rel":"author"}},[_c('span',{attrs:{"itemprop":"author","itemscope":"","itemtype":"http://schema.org/Person"}},[_c('span',{attrs:{"itemprop":"name"}},[_vm._v("puneethreddyhc")])])]),_vm._v(", "),_c('span',[_vm._v("Aug 10, 2020")])])])])]),_c('p',{staticClass:"excert"}),_c('p',{staticStyle:{"margin-bottom":"30px","color":"rgb(50, 56, 57)","font-family":"BentonSans, \"Helvetica Neue\", sans-serif","font-size":"15px"}},[_vm._v("There are always challenges when it comes to debugging applications. Node.js' asynchronous workflows add an extra layer of complexity to this arduous process. Although there have been some updates made to the V8 engine in order to easily access asynchronous stack traces, most of the time, we just get errors on the main thread of our applications, which makes debugging a little bit difficult. As well, when our Node.js applications crash, we usually need to"),_c('a',{staticStyle:{"color":"rgb(25, 105, 202)","text-decoration-line":"underline","transition":"color 0.1s linear 0s"},attrs:{"href":"https://www.ibm.com/developerworks/library/wa-ibm-node-enterprise-dump-debug-sdk-nodejs-trs/index.html"}},[_vm._v("rely on some complicated CLI tooling to analyze the core dumps")]),_vm._v(".")]),_c('p',{staticStyle:{